.page-main {
  margin-bottom: 40px;
}

.page-main__wrapper {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: @tablet-width) {
  .page-main {
    margin-bottom: 0;
  }

  .page-main__wrapper {
    width: 708px;
    padding: 0 30px;
  }
}

@media (min-width: @desktop-width) {
  .page-main {
    margin-bottom: 0;
  }

  .page-main__wrapper {
    width: 1220px;
    padding: 0 40px;
  }
}
